let book_tabs = document.querySelector('.book-tabs')
if (book_tabs) {
  let btns = book_tabs.querySelectorAll('.book-tabs__nav-trigger')

  btns.forEach(btn => btn.addEventListener('click', handleNavTrigger))
  btns.forEach((btn, index) => {
    btn.setAttribute('id', `tab-label-${index + 1}`)
    btn.setAttribute('aria-controls', `tab-panel-${index + 1}`)
    if (index === 0) {
      btn.setAttribute('tabindex', '0')
      btn.setAttribute('aria-selected', 'true')
    } else {
      btn.setAttribute('tabindex', '-1')
      btn.setAttribute('aria-selected', 'false')
    }
  })

  let panels = book_tabs.querySelectorAll('.book-tabs__panel')
  panels.forEach((panel, index) => {
    panel.setAttribute('aria-labelledby', `tab-label-${index+1}`)
    panel.setAttribute('id', `tab-panel-${index + 1}`)
    panel.setAttribute('tabindex', '-1')
    if (index === 0) {
      panel.setAttribute('aria-hidden', 'false')
    } else {
      panel.setAttribute('aria-hidden', 'true')
    }
  })

  function handleNavTrigger(e) {
    let target = e.currentTarget
    if (target.parentElement.classList.contains('is-active')) return
    btns.forEach(btn => {
      btn.parentElement.classList.remove('is-active')
      btn.setAttribute('tabindex', '-1')
      btn.setAttribute('aria-selected', 'false')
    })
    target.parentElement.classList.add('is-active')
    target.setAttribute('tabindex', '0')
    target.setAttribute('aria-selected', 'true')
    let id = target.id
    panels.forEach(panel => {
      panel.classList.remove('is-current')
      panel.setAttribute('aria-hidden', 'true')
    })
    let current = book_tabs.querySelector(`[aria-labelledby=${id}]`)
    current.classList.add('is-current')
    current.setAttribute('aria-hidden', 'false')
  }
}
